<template>
  <div class="p5ParticleArray">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import P5NavBar from "@/components/P5NavBar.vue";

export default {
  components: {
    Header,
    P5NavBar
  },
  data() {
    return {
      script: null,
      ps: null,
      canvas: null,
      particles: []
    };
  },
  mounted() {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);
      };

      p.draw = () => {
        p.background(255);

        this.particles.push(
          new Particle(p.createVector(p.width / 2, p.height / 2))
        );

        for (let particle of this.particles) {
          particle.run();
        }

        // remove dead particles
        this.particles = this.particles.filter(particle => !particle.isDead());
      };

      class Particle {
        constructor(l) {
          this.location = l.copy();
          this.velocity = p.createVector(p.random(-3, 3), p.random(-2, 0));
          this.acceleration = p.createVector(0, 0.1);
          this.lifespan = 1.0;
        }

        run() {
          this.update();
          this.display();
        }

        update() {
          this.velocity.add(this.acceleration);
          this.location.add(this.velocity);
          this.lifespan -= 0.01;
        }

        display() {
          p.stroke(`rgba(57, 81, 105, ${this.lifespan})`);
          p.strokeWeight(2);
          p.fill(`rgba(66, 185, 131, ${this.lifespan})`);
          p.ellipse(this.location.x, this.location.y, 32, 32);
        }

        isDead() {
          if (this.lifespan < 0.0) {
            return true;
          } else {
            return false;
          }
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require("p5");
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>

<style lang="scss" scoped>
.canvasP5 {
  display: block;
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.p5ParticleArray {
  user-select: none;
}
</style>
